"use client";
import React, { useState, useEffect } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";

// ۱. وارد کردن کامپوننت ظاهری از سیستم طراحی
import { SidebarMenuItem } from "@my-ds/design-system"; 

// ۲. آیکون‌ها و کامپوننت‌های محلی
import {
  FaCog,
  FaTimes,
  FaShieldAlt,
  FaStore,
  FaTicketAlt,
  FaChevronDown, // این دیگر در SidebarMenuItem مدیریت می‌شود
  FaBox,
  FaFlag,
  FaListAlt,
  FaAward,
  FaComments,
  FaSitemap,
  FaGavel,
  FaImages,
  FaVideo,
  FaLayerGroup,
  FaClipboardList,
  FaQuestionCircle,
} from "react-icons/fa";
import SidebarPinButton from "./SidebarPinButton"; // کامپوننت دکمه پین کردن

// ۳. داده‌های منو (مختص اپلیکیشن)
const menuItems = [
    {
        label: "فروشگاه‌ها",
        icon: FaStore,
        submenu: [
          { href: "/pages/Shops", label: "لیست فروشگاه‌ها", icon: FaListAlt },
          { href: "/pages/Shops/Badges", label: "بج ها", icon: FaAward },
          { href: "/pages/Shops/Comments", label: "کامنت ها", icon: FaComments },
          {
            href: "/pages/Shops/ShopCategory",
            label: "دسته بندی فروشندگان",
            icon: FaSitemap,
          },
          {
            href: "/pages/Shops/ConfirmRule",
            label: "قوانین تایید فروشگاه",
            icon: FaGavel,
          },
          {
            href: "/pages/Shops/Images",
            label: "عکس های ارسالی",
            icon: FaImages,
          },
          {
            href: "/pages/Shops/Videos",
            label: "ویدیو های ارسالی",
            icon: FaVideo,
          },
          {
            label: "گزارش‌ها",
            icon: FaFlag,
            submenu: [
              {
                href: "/pages/Shops/Report/ReportType",
                label: "انواع گزارش",
                icon: FaLayerGroup,
              },
              {
                href: "/pages/Shops/Report/Reports",
                label: "گزارش ها",
                icon: FaFlag,
              },
              {
                href: "/pages/Shops/Report/ReportSubjects",
                label: "موضوعات گزارش ها",
                icon: FaClipboardList,
              },
            ],
          },
        ],
        
      },
      {
        label: "تیکت‌ها",
        icon: FaTicketAlt,
        submenu: [{ href: "/pages/Tickets", label: "لیست تیکت‌ها", icon: FaListAlt }],
      },
     
      {
     label: "بانک",
        icon: FaStore,
        submenu: [
          { href: "/pages/Bank/Banks", label: "لیست بانک ها", icon: FaListAlt },
          { href: "/pages/Bank/Banks", label: "ارز ها", icon: FaListAlt },
        ],
      },
      {
        label: "سوالات متداول",
        icon: FaQuestionCircle,
        submenu: [
          { href: "/pages/FAQ", label: "مدیریت سوالات متداول", icon: FaQuestionCircle },
        ]
      }
];

// ۴. تابع کمکی برای تشخیص آیتم فعال
const isSubmenuActive = (submenu: any[], pathname: string): boolean => {
  if (!submenu) return false;
  return submenu.some((item) => {
    if (item.href === pathname) return true;
    if (item.submenu) return isSubmenuActive(item.submenu, pathname);
    return false;
  });
};

// ۵. کامپوننت بازگشتی برای رندر کردن آیتم‌های منو
const RecursiveMenuItem = ({ item, isExpanded, pathname, level = 0 }: any) => {
  const hasSubmenu = item.submenu && item.submenu.length > 0;
  const isActiveOrHasActiveChild = item.href === pathname || isSubmenuActive(item.submenu, pathname);
  const [isSubmenuOpen, setIsSubmenuOpen] = useState(isActiveOrHasActiveChild);

  useEffect(() => {
    if (isActiveOrHasActiveChild) {
      setIsSubmenuOpen(true);
    }
  }, [isExpanded, isActiveOrHasActiveChild]);

  const toggleSubmenu = () => {
    if (isExpanded) {
      setIsSubmenuOpen(!isSubmenuOpen);
    }
  };

  const IconComponent = item.icon || FaListAlt;

  // اگر آیتم یک لینک مستقیم است
  if (item.href) {
    // --- ✨ تغییرات کلیدی اینجا هستند ---
    return (
      // ۱. تگ <a> حذف شد.
      // ۲. پراپرتی‌های passHref و legacyBehavior حذف شدند.
      // ۳. SidebarMenuItem حالا فرزند مستقیم Link است.
      <Link href={item.href}>
        <SidebarMenuItem
          label={item.label}
          icon={<IconComponent />}
          isActive={pathname === item.href}
          isExpanded={isExpanded}
          level={level}
        />
      </Link>
    );
  }}


// ۶. کامپوننت اصلی و هوشمند Sidebar
const Sidebar = ({ isSidebarOpenForMobile, onCloseMobile }: any) => {
  const pathname = usePathname();
  const [isPinned, setIsPinned] = useState(false);
  const [isHovering, setIsHovering] = useState(false);

  const isDesktopExpanded = isPinned || isHovering;
  const shouldBeFullyOpen = isSidebarOpenForMobile || isDesktopExpanded;

  return (
    <>
      {/* Mobile Overlay */}
      {isSidebarOpenForMobile && (
        <div
          className="fixed inset-0 bg-black/40 z-30 md:hidden"
          onClick={onCloseMobile}
        ></div>
      )}

      {/* Main Sidebar Container */}
      <aside
        onMouseEnter={() => window.innerWidth > 768 && setIsHovering(true)}
        onMouseLeave={() => window.innerWidth > 768 && setIsHovering(false)}
        className={`bg-white dark:bg-slate-800 z-40 flex flex-col flex-shrink-0 transition-all duration-300 ease-in-out
                    fixed right-0 top-0 bottom-0 md:relative md:top-auto md:bottom-auto md:h-full
                    ${
                      isSidebarOpenForMobile
                        ? "translate-x-0 w-64"
                        : "translate-x-full"
                    } md:translate-x-0 
                    ${
                      !isSidebarOpenForMobile &&
                      (isDesktopExpanded ? "w-64" : "w-20")
                    }`}
      >
        <button
          onClick={onCloseMobile}
          aria-label="بستن منو"
          className="absolute top-4 left-4 md:hidden p-1 z-[41] text-slate-500 dark:text-slate-300"
        >
          <FaTimes size={22} />
        </button>

        {/* Sidebar Header */}
        <div className="flex items-center justify-center h-16 border-b border-slate-200 dark:border-slate-700 flex-shrink-0 relative">
          <div
            className={`hidden md:block transition-opacity duration-200 ${
              isHovering ? "opacity-100" : "opacity-0 pointer-events-none"
            }`}
          >
            <SidebarPinButton
              isPinned={isPinned}
              onClick={() => setIsPinned(!isPinned)}
            />
          </div>
          <div className="relative w-full flex items-center justify-center">
            <FaShieldAlt
              className={`text-3xl text-indigo-500 transition-opacity duration-200 ${
                !shouldBeFullyOpen ? "opacity-100" : "opacity-0"
              }`}
            />
            <span
              className={`font-bold text-lg whitespace-nowrap text-slate-800 dark:text-white absolute transition-opacity duration-200 ${
                shouldBeFullyOpen ? "opacity-100" : "opacity-0"
              }`}
            >
              پنل مدیریت
            </span>
          </div>
        </div>

        {/* Navigation Menu */}
        <nav className="flex-1 mt-4 space-y-1 px-2 overflow-y-auto overflow-x-hidden">
          {menuItems.map((item, index) => (
            <RecursiveMenuItem
              key={index}
              item={item}
              isExpanded={shouldBeFullyOpen}
              pathname={pathname}
            />
          ))}
        </nav>

        {/* Sidebar Footer */}
        <div className="p-2 border-t border-slate-200 dark:border-slate-700 flex-shrink-0">
          <RecursiveMenuItem
            item={{ href: "/settings", label: "تنظیمات", icon: FaCog }}
            isExpanded={shouldBeFullyOpen}
            pathname={pathname}
          />
        </div>
      </aside>
    </>
  );
};

export default Sidebar;